import React, {useState} from "react";
import {useInput} from "../../../hooks/useInput";
import ShadowWrapper from "../ShadowWrapper";
import WindowBody from "../WhiteWrapper";
import {CloseIcon} from "../../../icons";
import SelectInput from "../../Inputs/SelectInput";
import styles from "./booking.module.scss"
import InputWrapper from "../../Inputs/InputWrapper";
import {useAppDispatch} from "../../../app/hooks";
import {handleBooking, handleLogin} from "../../../features/modals/modalsSlice";
import RedButton from "../../Buttons/RedButton";
const BookingWindow = () => {
    const dispatch = useAppDispatch()
    return (
        <ShadowWrapper>
            <WindowBody className={`${styles.window} f-column`}>
                <div className="w-100p d-f jc-end">
                    <div onClick={() => dispatch(handleBooking())} className={"closeWrapper"}>
                        <CloseIcon isDark={true}/>
                    </div>
                </div>
                <div className="f-column gap-30">
                    <div className="f-column gap-20">
                        <h2 > </h2>
                        <div className="f-column">
                            <SelectInput labelText={"  ()"} selectHandler={(selected) => {
                                console.log(selected)}}  items={["", ""]}/>
                        </div>
                        <div className="f-row-betw gap-20 flex-wrap">
                            <SelectInput defaultCurrent={1} className={"f-1"} labelText={""} selectHandler={(selected) => {
                                console.log(selected)}}  items={["18:30", "19:30"]}/>
                            <SelectInput defaultCurrent={0} className={"f-1"} labelText={" "} selectHandler={(selected) => {
                                console.log(selected)}}  items={["2", "3"]}/>
                        </div>
                        <div className="f-column gap-15">
                            <InputWrapper placeholder={""} labelText={" "} />
                        </div>
                        <div className="f-column gap-15">
                            <InputWrapper placeholder={"+7"} labelText={"  ()"} />
                        </div>
                    </div>
                    <div className="f-column gap-15">
                        <RedButton disabled={true} className={"pd-10-0"}></RedButton>
                        <div className={"caption txt-center"}>,   <a href="">   
                                 </a></div>
                    </div>

                </div>


            </WindowBody>
        </ShadowWrapper>
    );
};

export default BookingWindow;